import React from 'react'
import Drawcard from './Drawcard'
import './Draw.css'

const buttonTexts = ['      24/7  ', '      '];
const buttonText = ['      ', '         '];
const textbutton = ['   24/7                     ', '                 .']
const textsbutton = ['                       ', '        .                       ']

export default function Draw() {
    return (
        <div className='draw' style={{ direction: 'rtl' }}>
            <h4 className='h'> </h4>
            <div className='drawdon'>
                {buttonTexts.map((text, index) => (
                    <Drawcard
                        text={text}
                        p={textbutton[index]}
                    />
                ))}
            </div>

            <div className='drawdon'>
                {buttonText.map((text, index) => (
                    <Drawcard
                        text={text}
                        p={textsbutton[index]}
                    />
                ))}
            </div>
        </div>
    )
}
