import React from 'react'
import './Title.css'
import car from '../components/image/car.webp'

export default function Title() {
    return (
        <div className="Title" id='titel'>
            <div className='title1'>
                <img className='car' src={car}></img>
                <div className="title2">
                    <h3 style={{ direction: 'rtl' }}>   </h3>
                    <p className='fontp' style={{ direction: 'rtl' }}>         <br className='Description1' />        .  <br className='Description1' /> &nbsp;<br className='Description2' />
                                   <br className='Description1' />     .      <br className='Description2' /><br className='Description1' /> 
                              .
                    </p>
                </div>
            </div>
            <div className='titl'>
                <div className='title1'>
                    <div className="title2">
                        <h3 style={{ direction: 'rtl' }}> </h3>
                        <p className='fontp' style={{ direction: 'rtl' }}>                      <br className='Description2'  /><br className='Description1' />
                              .                .
                        </p>
                    </div>
                    <h3 className='txt'>    </h3>

                </div>

            </div>
        </div>
    )
}
