import React from 'react'
import './Saidbar.css'
export default function Saidbar({ onServiceClick, onCarClick, onaboutusClick, oncallClick, onquestionsClick, onhomeClick, onCloseClick}) {
    return (
        <div className='saidbar'>
                <i class="fa fa-remove" id='close' onClick={onCloseClick}></i>
                <div><button className='home1' onClick={onhomeClick}></button></div>
                <div><button className='service1' onClick={onServiceClick}> </button></div>
                <div><button className='service1' onClick={onCarClick}></button></div>
                <div><button className='service1' onClick={onaboutusClick}> </button></div>
                <div><button className='service1' onClick={oncallClick}></button></div>
                <div> <button className='quesction2' onClick={onquestionsClick}></button></div>
            </div>
    )
}

